import { RiDeleteBin6Line } from "react-icons/ri";
import { CgMathPlus } from "react-icons/cg";
import { useEffect, useState } from "react";
import * as postList from "./styles/blog-posts.module.css";

const PostsList = (props) => {
  const [posts, setPosts] = useState();

  const getPosts = () => {
    fetch("https://jsonplaceholder.typicode.com/posts")
      .then((response) => response.json())
      .then((json) => setPosts(json));
  };

  const deletePost = (postId) => {
    fetch(`https://jsonplaceholder.typicode.com/posts/${postId}`, {
      method: "DELETE"
    }).then((response) => {
      getPosts();
      alert("Post deleted! (Not really ðŸ˜›)");
    });
  };

  useEffect(() => {
    getPosts();
  }, []);

  return (
    <section className={"blog-posts"}>
      <section className={postList["blog-posts-header"]}>
        <h2>Blog Posts</h2>
        <a
          href={"new-post"}
          className={postList["create-new-post-icon"]}
          onClick={() => {
            props.setCurrentView("create-new-post");
          }}
        >
          <CgMathPlus />
        </a>
      </section>
      {posts && (
        <ul className={postList["posts-list"]}>
          {posts.map((post, i) => (
            <li key={i}>
              <div className={postList["post-header"]}>
                <h2>{post.title}</h2>
              </div>
              <div className={postList["post-body"]}>
                <p>{post.body}</p>
              </div>
              <div
                title={"Delete post"}
                className={postList["delete-post-icon"]}
                onClick={() => {
                  deletePost(post.id);
                }}
              >
                <RiDeleteBin6Line />
              </div>
            </li>
          ))}
        </ul>
      )}
    </section>
  );
};

export default PostsList;
